  @media screen and (max-width: 960px) {
    

.navbar {
  background: #000;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: absolute;
  width: 100%;
  margin:0px;
  top: 0;
  z-index: 999;
}

    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 75px;
      left: -150%;
      opacity: 1;
      transition: all 0.5s ease;

    }
  
    .nav-menu.active {
      z-index: 5000;
      margin:0;
      background: #000;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      padding-left: 0;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
      margin-left:0;
      padding-left: 0;
    }
  
    .nav-links:hover {
      background-color:#000;
      color: #FFF;
      color: var(--details);
      border-radius: 0;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(10%, 45%);
    }
  
    .menu-icon {
      color: #FFF;
      color: var(--details);
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #FFF;
      color: var(--details);
      font-size: 2rem;
    }
  
    .nav-links-mobile {
      display: block;
      text-align: center;
      margin: 2rem auto;
      border-radius: 4px;
      width: 80%;
      text-decoration: none;
      font-size: 1.5rem;
      background-color: transparent;
      color:#FFF;
      color:var(--details);
      padding: 14px 20px;
      border: 1px solid #fff;
      transition: all 0.3s ease-out;
    }
  
    .nav-links-mobile:hover {
      background: #000;
      color: #FFF;
      color: var(--details);
      transition: 250ms;
    }
  }


