.text-center{
    margin-top: 0em;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
}
#gallery{
    border-bottom: 2px solid #000;
    width: 280px;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    padding: 10px;
    padding-top: 0;
    margin-bottom: 30px;
    font-family: 'Playfair Display SC', serif;
}
#gallery h1{
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    font-size: 50px!important;
    font-family: 'Playfair Display SC', serif;
}

.img-fluid{
	height:350px;
    width: 330px;
    border: 15px solid #fff;
    border-bottom: 70px solid #fff;
    box-shadow: 0px 25px 30px rgba(0,0,0,0.3);
    margin: 9px;
}
.gallery{
    text-align:center;
    align-items: center;
    justify-content: center;
	margin-top: 10px;
	color: #fff;
}

@media only screen and (min-width: 768px) {
.img-fluid:hover{
	transform: scale(1.05);
    transition: 1s;
}

}

@media (max-width: 768px){
    .img-fluid{
        width:280px;
        height: 330px;
    }

    #gallery{
        border-bottom: 1px solid #000;
        width: 260px;
        text-align: center;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        padding: 10px;
        margin-bottom: 30px;
        font-family: 'Playfair Display SC', serif;
    }
}
